import React from "react";
import Phone from "./Phone";
import PinCode from "./PinCode";
import CreditCard from "./CreditCard";
const EVMaskedControl = (props) => {
  const { control, ...rest } = props;
  switch (control) {
    case "phone":
      return <Phone {...rest} />;
    case "pincode":
      return <PinCode {...rest} />;
    case "credit":
      return <CreditCard {...rest} />;
    default:
      return null;
  }
};

export default EVMaskedControl;
